<template>
  <div class="ui fullscreen modal hide">
    <i class="close icon" @click="$_close"></i>
    <div class="header" v-html="modalOpts?modalOpts.title:' '" @dblclick="isShowContent=!isShowContent">
    </div>
    <div v-show="isShowContent" class="scrolling content" :style="contentStyle">
      <!--在component内的vue中，调用$emit('callModal', {fnName: paramObject})，以触发$_invokeCallbackSet-->
      <component ref="content" :componentUpdated="isMounted=true" :is="modalBody"
                 :opts="modalOpts.opts"
                 :query="modalOpts.query"
                 :opener="modalOpener" :modal="modal">
        正在加载...
      </component>
    </div>
    <!-- 采用"actions"样式类，其内部的button会被semantic-ui框架加上关闭事件，这里改用gl-modal-actions重写 -->
    <div class="gl-modal-actions" style="text-align: center">
      <!--<div class="ui mini button" :class="$gl.ui.color.primary">保存</div>-->
      <!--<div class="ui mini button" :class="$gl.ui.color.negative" @click="$_cancel">取消</div>-->

      <!--<div v-if="!toolbar" v-for="(item,key) in actions" class="ui mini button" :key="key"-->
      <!--:class="$gl.ui.color[item.color]"-->
      <!--@click="$_doAction(key)">-->
      <!--{{item.title}}-->
      <!--</div>-->
      <gl-toolbar v-if="toolbar" v-bind="toolbar" :css="{center:true,dividing:false}" :ctx="content"></gl-toolbar>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      modalId: String,
      modalOpener: {
        type: Object
      },
      // modalHeader:  {title: ''},
      // modalFooter: null,
      modalBody: Function,
      modalOpts: {
        type: Object,
        default() {
          return {title: '', actions: [], contentStyle: {}}
        }
      },
      // 回调事件集合 格式如：{selected:function(data){ // do something }}
      callbackSet: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    data: function () {
      return {
        externalActions: {},
        isModal: true,
        // 页面下方的操作按钮，通过action也可以加入一些事件，但与callbackSet不一样，actions会在modal页面中动态生成按钮
        // 在执行完该按钮绑定的事件之后，会再检查callbackSet是否有同名的方法，有的话，会执行该回调方法
        actions: {},
        isMounted: false,
        isShowContent: true,
        contentStyle: {},
        toolbar: undefined
      }
    },
    computed: {
      modal() {
        console.log('gl-modal > Index > computed modal: ', this)
        return this
      },
      content() {
        console.log('gl-modal > Index > computed modalContent: ', this.$refs.content)
        return this.$refs.content
      }
    },
    created: function () {
      console.log('gl-modal > Index> opener: ', this.modalOpener)
    },
    mounted: function () {
      this.toolbar = undefined
      this.contentStyle = {padding: '1.5em', 'overflow-y': 'auto'}
      $.extend(this.contentStyle, this.modalOpts.contentStyle)
      $(this.$el).draggable({cancel: '.ui.modal>.content'})
    },
    methods: {
      /**
       *  设置打开modal的来源组件
       *  便于后续直接调用opener的操作，例如，在gl-table中打开modal,当modal中保存并关闭窗口时，调用opener的刷新gl-table页面
       */
      $_setOpener(opener) {
        this.opener = opener
      },
      /**
       *  合并工具条
       *  @toolbar 工具配置
       */
      $_appendToolbar(toolbar) {
        this.toolbar = toolbar
        return true
      },
      /**
       * 关键窗口，并调用钩子 close
       * @param e
       */
      $_close: function (e) {
        let value = {}
        if (typeof this.$refs.content.$_getValue === 'function') {
          value = this.$refs.content.$_getValue()
        }
        console.log('gl-modal > Index > $_close > get value: ', value)
        $(this.$el).modal('hide')
        if (typeof this.callbackSet.close === 'function') {
          this.callbackSet.close(e, value)
        }
        // TODO 删除id
        return value
      },
      /**
       * 关键窗口，并调用钩子 cancel
       * @param e
       */
      $_cancel: function (e) {
        console.log('gl-modal > Index > $_cancel()')
        $(this.$el).modal('hide')
        if (typeof this.callbackSet.cancel === 'function') {
          this.callbackSet.cancel(e)
        }
        // TODO 删除id
      },
      $_callback: function (name, params) {
        let fn = this.callbackSet[name]
        if (!fn) {
          console.error('在modal的页面中，调用方法(' + name + ')失败，因为打开该modal时，未注册该方法，已注册的方法为：', this.callbackSet)
        } else {
          this.callbackSet[name](params)
        }
      },
      $_doAction: function (name, params) {
        let fn = this[name]
        if (!fn) {
          console.error('在modal的页面中，调用方法(' + name + ')失败，因为modal没有该操作按钮，当前actions为：', this.actions)
        } else {
          this[name](params)
          // 回调
          if (typeof this.callbackSet[name] === 'function') {
            this.callbackSet[name](params)
          }
        }
      },
      $_addAction: function ({name, title, color, fn}) {
        if (!this.$_checkAction(name)) {
          return
        }
        if (name === '$_cancel' || name === 'cancel') {
          this.actions[name] = {
            title: title || '取消',
            color: color || 'negative'
          }
          this[name] = fn || this.$_cancel
        } else if (name === '$_close' || name === 'close') {
          this.actions[name] = {
            title: title || '关闭',
            color: color || 'primary'
          }
          this[name] = fn || this.$_close
        } else {
          this.actions[name] = {
            title: title,
            color: color || 'primary'
          }
          this[name] = fn
        }
      },
      $_updateActions() {
        this.$forceUpdate()
      },
      $_checkAction: function (name) {
        if (name === '$_addAction' || name === '$_doAction' || name === '$_removeAction') {
          console.error('{name}是内置的action管理函数，不可添加或删除！')
          return false
        }
        return true
      }
    },
    components: {}
  }
</script>
<style scoped>
  /*.page-content {*/
  /*margin-left: 0;*/
  /*}*/

  .gl-modal-actions {
    background: #f9fafb;
    padding: 1rem 1rem;
    border-top: 1px solid rgba(34, 36, 38, .15);
  }
</style>
